<template>
  <div class="grid grid-cols-2 gap-6">
    <div class="col-span-2">
      <Input
        v-model="data.legal_business_name"
        placeholder="Legal Business Name"
        label="Legal Business Name"
        block
        :view="validation.legal_business_name.$invalid ? 'danger' : ''"
      />
      <small
        class="text-red-base block mt-2"
        v-for="(error, errorIndex) of validation.legal_business_name.$errors"
        :key="errorIndex"
        >{{ error.$message }}.
      </small>
    </div>
    <div class="col-span-2">
      <Input
        v-model="data.name"
        placeholder="Doing Business As (DBA)"
        label="Doing Business As (DBA)"
        block
        :view="validation.name.$invalid ? 'danger' : ''">
        <template #label>
          <div class="flex items-center gap-x-2">
            <p>Doing Business As (DBA)</p>
            <Tooltip placement="top">
              <template #content>
                <p class="text-sm max-w-sm">Your DBA (Doing Business As) is the name that will appear on your shipping labels for Mail-In shipments when Mail-In is enabled.</p>
              </template>
              <i class="material-icons">info</i>
            </Tooltip>
          </div>
        </template>
      </Input>
      <small
        class="text-red-base block mt-2"
        v-for="(error, errorIndex) of validation.name.$errors"
        :key="errorIndex"
        >{{ error.$message }}.
      </small>
    </div>
    <div>
      <SelectCountry
        @select="handleSelectCountry"
        placeholder="Select Country"
        label="Business Country"
        :countries="countries"
        no-border
        rounded
      />
      <small
        class="text-red-base block mt-2"
        v-for="(error, errorIndex) of validation.country_code.$errors"
        :key="errorIndex"
        >{{ error.$message }}.
      </small>
    </div>
    <div>
      <SelectCurrency
        @change="handleSelectCurrency"
        placeholder="Select Currency"
        label="Business Currency"
        :currencies="currencies"
      />
      <small
        class="text-red-base block mt-2"
        v-for="(error, errorIndex) of validation.currency.$errors"
        :key="errorIndex"
        >{{ error.$message }}.
      </small>
    </div>
    <div class="col-span-2">
      <Tooltip placement="top" :disabled="data?.country_code?.length > 0">
        <template #content>
          <p class="text-sm max-w-sm">Please select a country before searching for your business address.</p>
        </template>
        <Input
          :disabled="!data.country_code"
          v-gmap="address"
          v-model="data.address_text"
          placeholder="Search for your business address"
          label="Physical Business Street Address"
          block
          :view="validation.address_text.$invalid || showAddressError ? 'danger' : ''"
          @blur="handleAddressBlur"
          @focus-out="handleAddressBlur"
          @focus="handleAddressFocus"
          @input="handleAddressInput"
        />
      </Tooltip>
      <small
        v-if="showAddressError"
        class="text-red-base block mt-2"
      >
        Invalid address selection. Please select an address from the dropdown suggestions.
      </small>
      <small
        class="text-red-base block mt-2"
        v-for="(error, errorIndex) of validation.address_text.$errors"
        :key="errorIndex"
        >{{ error.$message }}.
      </small>
    </div>
    <div class="col-span-2">
      <Input 
        v-model="data.suite"
        block 
        label="Apartment, suite, unit, building, floor (optional)" />
    </div>
    <template v-if="data.country_code">
      <div class="col-span-2">
      <div
        :class="[
          'grid gap-4',
          'gap-4 grid-cols-1',
          !countriesWithoutState.includes(data.country_code) ? 'md:grid-cols-3' : 'md:grid-cols-2',
        ]">
        <div>
          <Input
            v-model="data.city"
            label="City"
            placeholder="City"
            block
            :view="(validation?.city?.$invalid && 'danger') || 'default'"
          />
          <span
            class="text-red-base my-2 block"
            v-for="error of validation?.city?.$errors"
            >{{ error.$message }}.
          </span>
        </div>
        <div v-if="!countriesWithoutState.includes(data.country_code)">
          <Input
            v-model="data.state"
            label="State"
            placeholder="State"
            block
            :view="(validation?.state?.$invalid && 'danger') || 'default'"
          />
          <span
            class="text-red-base my-2 block"
            v-for="error of validation?.state?.$errors"
            >{{ error.$message }}.
          </span>
        </div>
        <div>
          <Input
            v-model="data.zip"
            :label="data.country_code === 'GB' ? 'Postcode' : 'Postal Code'"
            :placeholder="data.country_code === 'GB' ? 'Postcode' : 'Postal Code'"
            block
            :view="(validation?.zip?.$invalid && 'danger') || 'default'"
          />
          <span
            class="text-red-base my-2 block"
            v-for="error of validation?.zip?.$errors"
            >{{ data.country_code === 'GB' ? 'Postcode' : 'Postal Code' }} {{ error.$message }}.
          </span>
        </div>
      </div>
      </div>
    </template>

  </div>
</template>
<script>
import { defineComponent, computed, ref, watch, reactive, watchEffect } from 'vue'

// Composables
import { usePurchasingMethod } from 'composable/onboarding'

// Components
import Input from 'bbUi/components/input/input.vue'

import SelectCountry from 'components/SelectCountry.vue'
import SelectCurrency from 'components/SelectCurrency.vue'

// Utils
import { useVuelidate } from '@vuelidate/core'
import { isValidCountryCode } from 'utils/is'
import { countriesWithoutState } from 'constants/countries'

export default defineComponent({
  components: {
    Input,
    SelectCountry,
    SelectCurrency
  },
  props: {
    data: {
      type: Object,
      default: () => ({
        name: null,
        country_code: null,
        country_name: null,
        currency: null,
        currency_symbol: null,
        address: null,
        city: null,
        zip: null,
        state: null,
        suite: null,
        address_text: null,
        location_type: null,
        valid: false
      })
    },
    countries: {
      type: Array,
      default: () => []
    },
    currencies: {
      type: Array,
      default: () => []
    }
  },
  emits: ['update:data'],
  setup(props, { emit }) {
    const { validationRules, actionSetCurrency } = usePurchasingMethod()
    const addressTouched = ref(false)
    const addressFocused = ref(false)
    
    const data = computed({
      get: () => props.data,
      set: (newValue) => emit('update:data', newValue)
    })

    const showAddressError = computed(() => {
      return addressTouched.value && 
             data.value.address_text && 
             data.value.address_text.trim() !== '' && 
             !data.value.valid
    })

    const handleAddressFocus = () => {
      addressFocused.value = true
    }

    const handleAddressBlur = () => {
      addressFocused.value = false
      if (data.value.address_text && data.value.address_text.trim() !== '') {
        addressTouched.value = true
      }
    }

    const handleAddressInput = () => {
      if (!data.value.address_text || data.value.address_text.trim() === '') {
        addressTouched.value = false
      }
    }

    watch(() => data.value.valid, (newValid) => {
      if (newValid) {
        addressTouched.value = false
      }
    })

    watch(() => data.value.address_text, (newValue) => {
      if (!newValue || newValue.trim() === '') {
        addressTouched.value = false
      }
    })

    watchEffect(() => {
      if (data.value.country_code) {
        Object.assign(address, {
          restrictCountries: isValidCountryCode(data.value.country_code)
            ? [data.value.country_code]
            : []
        })
      }
    })

    watch(() => data.value.country_code, async (newCountryCode, oldCountryCode) => {
      if (oldCountryCode !== undefined && newCountryCode !== oldCountryCode) {
        data.value.address = null
        data.value.address_text = null
        data.value.city = null
        data.value.state = null
        data.value.zip = null
        data.value.suite = null
        data.value.valid = false
        addressTouched.value = false
        addressFocused.value = false
      }
    })

    const handleSelectCountry = (country) => {
      data.value.country_code = country.country_code
      data.value.country_name = country.nicename
      addressTouched.value = false
      addressFocused.value = false
      data.value.valid = false
      data.value.address_text = null
    }
    
    const handleSelectCurrency = (currency) => {
      actionSetCurrency({ symbol: currency.symbol, currency: currency.currency_code })
      data.value.currency = currency.currency_code
      data.value.currency_symbol = currency.symbol
    }
    
    const validate = () => {
      validation.value.$validate()
      return !validation.value.$invalid
    }
    
    const validation = useVuelidate(validationRules, data, {
      $lazy: true
    })

    const address = reactive({
      valid: 'valid',
      route: 'address',
      locality: 'city',
      administrative_area_level_1: 'state',
      formatted_address: 'address_text',
      postal_code: 'zip',
      geo: {
        lat: 'lat',
        lng: 'lon'
      },
      restrictCountries: []
    })

    return {
      data,
      addressTouched,
      addressFocused,
      showAddressError,
      address,
      handleAddressFocus,
      handleAddressBlur,
      handleAddressInput,
      handleSelectCountry,
      handleSelectCurrency,
      validate,
      validation,
      countriesWithoutState
    }
  }
})
</script>