<template>
  <div class="flex h-full">
    <div class="lg:my-auto mx-auto px-20 2xl:mx-56">
      <div class="max-w-4xl mx-3">
        <h2 class="text-3xl font-semibold">
          How do you intend on purchasing Items?
        </h2>
        <p class="text-black my-4">
          This will determine how your customers can sell their items to you
          through your buyback widget. Choose your primary method and you can
          always add the other one later.
        </p>
        <!--
          <div v-if="showAlert" class="bg-grey-01 inline-block px-4 py-2 rounded-md">
            <div class="flex items-center gap-x-4">
              <span class="material-icons text-4xl">info</span>
              <p>Your access email will still same as previous account</p>
              <span class="material-icons my-auto ml-auto cursor-pointer" @click="closeAlert">close</span>
            </div>
          </div>
        -->
      </div>
      <div class="max-w-3xl">
        <div class="mt-8 mb-6 w-full">
          <BuybackType v-model="data.location_type" class="w-full" />
        </div>
        <div class="w-full">
          <form @submit.prevent="handleSubmit">
            <transition name="slide-fade">
              <FormPurchasingMethod
                v-show="isShowForm"
                ref="refFormPurchasingMethod"
                v-model:data="data"
                :countries="countries"
                :currencies="currencies"
              />
            </transition>
            <Button
              class="my-6"
              :loading="loading"
              color="teal"
              :disabled="isIncompleteForm"
              >Continue <i class="material-icons ml-2">east</i></Button
            >
          </form>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import { computed, defineComponent, onMounted, onUnmounted, ref } from 'vue'
import { useRoute, useRouter } from 'vue-router'

// Composables
import { usePurchasingMethod } from 'composable/onboarding'

// Components
import Button from 'bbUi/components/button/button.vue'

import BuybackType from 'components/Onboarding/BuybackType.vue'
import FormPurchasingMethod from 'components/Onboarding/FormPurchasingMethod.vue'

export default defineComponent({
  components: {
    Button,
    BuybackType,
    FormPurchasingMethod
  },
  async setup() {
    const refFormPurchasingMethod = ref(null)
    const {
      data,
      loading,
      isIncompleteForm,
      isShowForm,
      countries,
      currencies,
      action,
      actionCurrencies,
      actionCountries
    } = usePurchasingMethod()
    const route = useRoute()
    const router = useRouter()
    // const myAlert = ref(true)
    // const showAlert = computed(() => 
    //   route.path.includes('new-company') && myAlert.value
    // )

    const handleSubmit = async () => {
      if (refFormPurchasingMethod.value) {
        const isValid = refFormPurchasingMethod.value.validate()
        if (isValid) {
          if (route.name.includes('newCompany')) {
            data.token = localStorage.getItem('token')
          } else if (data.token) delete data.token

          await action(() => {
            router.push({
              name: route.name.includes('newCompany')
                ? 'newCompanySyncProducts'
                : 'onboardingSyncProducts'
            })
          }, route.name.includes('newCompany'))
        }
      }
    }
    const handleReload = (e) => {
      let count = 0
      Object.keys(data).forEach(key => {
        if (key !== 'valid' && key !== 'location_type' && !!data[key]) {
          if (!!(data[key].toString()).length) {
            count += 1
          }
        }
      })
      if (count > 0) return e.preventDefault()

      return false
    }
    // const closeAlert = () => {
    //   myAlert.value = false
    // }

    onMounted(() => {
      window.addEventListener('beforeunload', handleReload)
    })
    onUnmounted(() => {
      window.removeEventListener('beforeunload', handleReload)
    })

    await Promise.all([actionCurrencies(), actionCountries()])

    return {
      refFormPurchasingMethod,
      data,
      loading,
      countries,
      currencies,
      // showAlert,
      isIncompleteForm,
      isShowForm,
      // closeAlert,
      handleSubmit
    }
  }
})
</script>
