<template>
  <div class="buyback-type">
    <div
      v-for="buybackType in buybackTypeOptions"
      :key="buybackType.value"
      @click="handleSelectBuybackType(buybackType.value)"
      :class="[
        'buyback-type__option',
        buybackType.is_selected ? 'buyback-type__option--selected' : ''
      ]"
    >
      <div
        class="buyback-type__icon"
        :style="`-webkit-mask-image: url('${buybackType.icon_url}')`"
      ></div>
      <div class="buyback-type__title">{{ buybackType.name }}</div>
      <div class="buyback-type__description">
        {{ buybackType.description }}
      </div>
    </div>
    <Transition name="fade">
      <div
        v-show="selectedBuybackType == 1"
        class="bg-blue-lighten text-blue-04 py-3 px-5 rounded-lg flex gap-x-4">
        <i class="material-icons">info</i>
        <p>
          <b>You must set up shipping integration</b>
          before generating shipping labels. You can find instructions on how to set it up
          <a href="https://help.reusely.com/en/articles/9682274-shipping-integration-for-mail-in-offers" target="_blank" class="font-semibold text-blue-04 underline hover:text-blue-03">here</a>.
        </p>
      </div>
    </Transition>
  </div>
</template>
<script>
import { defineComponent, computed } from 'vue'
export default defineComponent({
  props: {
    modelValue: {
      type: Number,
      default: () => null
    }
  },
  emits: ['update:modelValue'],
  setup(props, { emit }) {
    const selectedBuybackType = computed(() => props.modelValue)
    const buybackTypeOptions = computed(() => {
      const typeOptions = [
        {
          name: 'Mail-In',
          description:
            'Automatically generate return shipping labels for your customers',
          icon_url: '/icon/onboarding/buyback-type_mailin.svg',
          value: 1
        },
        {
          name: 'In-Store',
          description: 'Increase trade-in traffic to your local store',
          icon_url: '/icon/onboarding/buyback-type_instore.svg',
          value: 2
        }
      ]
      return typeOptions.map((typeOption) => ({
        ...typeOption,
        is_selected: selectedBuybackType.value === typeOption.value
      }))
    })
    const handleSelectBuybackType = (value) => {
      emit('update:modelValue', value)
    }

    return {
      selectedBuybackType,
      buybackTypeOptions,
      handleSelectBuybackType
    }
  }
})
</script>
<style lang="scss" scoped>
.buyback-type {
  @apply my-4 inline-flex flex-wrap gap-4 items-stretch;
  $self: &;
  &__option {
    @apply transition duration-300 w-64 px-8 py-4 bg-white border border-grey-lighten text-center cursor-pointer rounded-lg;
    #{$self}__icon {
      @apply h-40 w-40 bg-grey-04 mx-auto;
      -webkit-mask-size: contain;
      -webkit-mask-position: center;
      -webkit-mask-repeat: no-repeat;
    }
    #{ $self }__title {
      @apply text-lg font-semibold my-2;
    }
    #{$self}__description {
      @apply text-xs;
    }
    &:hover,
    &--selected {
      @apply border-teal-04 text-teal-04;
      #{$self}__icon {
        @apply bg-teal-04;
      }
    }
  }
}
</style>
